import { useForm } from "react-hook-form";

const useOrderForm = () => {
  const form = useForm({
    defaultValues: {
      company_name: "",
      npwp_number: "",
      employees_amount: "",
      pic_name: "",
      pic_phone: "",
      pic_email: "",
      license_type: "professional",
      user_amount: "",
      notes: "",
      status: "DITERIMA",
    },
  });

  console.log(form.watch());
  return {
    form,
  };
};

export default useOrderForm;
